<template>
  <div id="user-list">
    <el-row class="warp">
      <el-col :span="24" class="warp-main" v-loading="loading" element-loading-text="">
        <!---->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
          <el-form :inline="true" :model="filters">
            <el-form-item>
              <el-input v-model="filters.name" placeholder="//" style="min-width: 240px;" @keyup.enter.native="handleSearch"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="handleSearch"></el-button>
            </el-form-item>
          </el-form>
        </el-col>

        <!---->
        <el-table :data="users" highlight-current-row v-loading="loading" style="width: 100%;">
          <el-table-column type="index" width="60">
          </el-table-column>
          <el-table-column prop="name" label="" width="120" sortable>
          </el-table-column>
          <el-table-column prop="nickname" label="" width="140" sortable>
          </el-table-column>
          <el-table-column prop="sex" label="" width="100" :formatter="formatSex" sortable>
          </el-table-column>
          <el-table-column prop="email" label="" width="260" sortable>
          </el-table-column>
          <el-table-column prop="addr" label="" sortable>
          </el-table-column>
        </el-table>

      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: "user-list",
  data() {
    return {
      filters: {
        name: ""
      },
      loading: false,
      users: [
        {
          nickname: "Mical",
          name: "",
          sex: "1",
          email: "123123@163.com",
          addr: ""
        },
        {
          nickname: "John",
          name: "",
          sex: "1",
          email: "123123@qq.com",
          addr: ""
        }
      ],
      total: 0,
      page: 1,
      limit: 10,
      loading: false
    };
  },
  methods: {
    //
    formatSex: function(row, column) {
      return row.sex == 1 ? "" : row.sex == 0 ? "" : "";
    },
    //
    handleCurrentChange(val) {
      this.page = val;
      this.search();
    },
    //
    handleSearch() {
      this.total = 0;
      this.page = 1;
      this.search();
    },
    //
    search: function() {
      let that = this;
      let params = {
        page: that.page,
        limit: 10,
        name: that.filters.name
      };
      //loading
      // that.loading = true;
      // API.findList(params)
      //   .then(
      //     function(result) {
      //       that.loading = false;
      //       if (result && result.users) {
      //         that.total = result.total;
      //         that.users = result.users;
      //       }
      //     },
      //     function(err) {
      //       that.loading = false;
      //       that.$message.error({
      //         showClose: true,
      //         message: err.toString(),
      //         duration: 2000
      //       });
      //     }
      //   )
      //   .catch(function(error) {
      //     that.loading = false;
      //     console.log(error);
      //     that.$message.error({
      //       showClose: true,
      //       message: "",
      //       duration: 2000
      //     });
      //   });
    }
  },
  components: {}
};
</script>

<style lang="scss" scoped>
</style>


