  methods: {
    //
    handleCurrentChange(val) {
      this.page = val;
      this.init();
    },
    //
    handleSearch() {
      this.total = 0;
      this.page = 1;
      this.search();
    },
    search() {
      let that = this;
      // let params = {
      //   page: that.page,
      //   name: that.filters.name
      // };
      // var url = this.HOST + requestPath;
      //loading
      // that.loading = true;
      // API.findList(params)
      //   .then(
      //     function(result) {
      //       that.loading = false;
      //       if (result && result.books) {
      //         that.total = result.total;
      //         that.books = result.books;
      //       }
      //     },
      //     function(err) {
      //       that.loading = false;
      //       that.$message.error({
      //         showClose: true,
      //         message: err.toString(),
      //         duration: 2000
      //       });
      //     }
      //   )
      //   .catch(function(error) {
      //     that.loading = false;
      //     console.log(error);
      //     that.$message.error({
      //       showClose: true,
      //       message: "",
      //       duration: 2000
      //     });
      //   });
    },
    selsChange: function(sels) {
      this.sels = sels;
    },
    init() {
      console.log(this.currentTab);
      let that = this;

      let index = that.initFormRules.findIndex(
        menu => menu.name === that.currentTab
      );
      // console.log(index);
      let url = that.HOST + that.initFormRules[index].url;
      let method = that.initFormRules[index].method;
      // console.log(url, method);

      if (method == "post") {
        let params = Qs.stringify({
          pageNum: that.page //1
        });
        //loading
        that.loading = true;
        //post
        that
          .axios({
            method: "post",
            url: url,
            data: params
          })
          .then(response => {
            console.log(response.data);
            that.items = response.data.message.list;
            that.total = response.data.message.list.length;
            that.loading = false;
          })
          .catch(response => {
            console.log(response.data);
            that.loading = false;
            that.$message.error({
              showClose: true,
              message: "",
              duration: 2000
            });
          });
      } else if (method == "get") {
        let data = {
          pageNum: that.page //1
        };
        //loading
        that.loading = true;
        //get
        that.axios
          .get(url, {
            params: data
          })
          .then(response => {
            console.log(response.data);
            that.items = response.data.message.list;
            that.total = response.data.message.list.length;
            that.loading = false;
          })
          .catch(response => {
            console.log(response.data);
            that.loading = false;
            that.$message.error({
              showClose: true,
              message: "",
              duration: 2000
            });
          });
      } else {
        that.items = "";
        that.total = 0;
        that.$message.error({
          showClose: true,
          message: "",
          duration: 2000
        });
      }
    }
  },
  activated() {
    this.init();
  },
  watch: {
    isRequested() {
      this.init();
      console.log(this.isRequested);
    }
  }
};
